.breadcrum{
    display: flex;
    align-items: center;
    gap:8px;
    color:#5e5e5e;
    font-size: 16px;
    font-weight: 600;
    margin:60px 170px;
    text-transform: capitalize;
}
@media(max-width:1280px){
    .breadcrum{
        margin:30px 50px;
        font-size: 14px;
    }
}
@media(max-width:1024px){
    .bredcrum{
       margin:30px 30px;
       font-size: 13px; 
    }   
}
@media(max-width:800px){
    .bredcrum{
       margin: 30px 30px;
       font-size: 13px;
    }
}
@media(max-width:500px){
    .bredcrum{
       font-size: 10px;
    }
}
